<template>
  <!-- Using components -->
  <div class="fluid-container WeddingPhotoPage">
    <div v-if="weddingphotoCategory == 1">Deka 1</div>
    <div v-else-if="weddingphotoCategory == 2">Deka 2</div>
    <div v-else-if="weddingphotoCategory == 3">Deka 3</div>
    <div v-else-if="weddingphotoCategory == 4">Deka 4</div>
    <div v-else-if="weddingphotoCategory == 5">Deka 5</div>
    <div v-else-if="weddingphotoCategory == 6">Deka 6</div>
    <div v-else>Deka</div>
    <button @click="goback">Goback</button>

    <div class="flipbook-viewport">
      <div class="container">
        <div class="flipbook">
          <div
            v-for="item in photolist"
            :key="item.id"
            class="pages"
            :class="item.class"
            :style="{ 'background-image' : 'url(' + item.url +')' }"
          ></div>
        </div>
      </div>
    </div>
    <!-- <div id="flipbook">
      <div
        v-for="item in photolist"
        :key="item.id"
        class="pages"
        :class="item.class"
        :style="{ 'background-image' : 'url(' + item.url +')' }"
      ></div>
    </div>-->
  </div>
  <!-- /container -->
</template>

<script>
import { type } from "os";
import "../assets/turnjs4/lib/turn.min";
import "../assets/turnjs4/lib/scissor.min";

export default {
  name: "WeddingPhoto",
  props: {
    msg: String
  },
  data() {
    return {
      name: "",
      fullWidth: document.documentElement.clientWidth,
      fullHeight: document.documentElement.clientHeight,
      breakpoints: 3 /*xl:4 lg:3 md:2 sm:1 xs:0*/,
      lastScrollTop: 0,
      photolist: [
        {
          class: "page",
          url: require("@/assets/turnjs4/double-page/pages/1.jpg")
        },
        {
          class: "double",
          url: require("@/assets/turnjs4/double-page/pages/2.jpg")
        },
        {
          class: "page",
          url: require("@/assets/turnjs4/double-page/pages/7.jpg")
        }
      ]
    };
  },
  created() {
    let vm = this;
    window.addEventListener("resize", vm.handleResize);
    vm.checkBreakPoint();
  },
  mounted() {
    let vm = this;
    vm.loadApp();
  },
  computed: {
    weddingphotoCategory() {
      let vm = this;
      return vm.$store.state.weddingphotoCategory;
    }
  },
  methods: {
    handleResize(event) {
      let vm = this;
      vm.checkBreakPoint();
    },
    checkBreakPoint() {
      let vm = this;
      vm.fullWidth = document.documentElement.clientWidth;
      vm.fullHeight = document.documentElement.clientHeight;
      if (vm.fullWidth >= 1200) {
        vm.breakpoints = 4;
      } else if (vm.fullWidth >= 992) {
        vm.breakpoints = 3;
      } else if (vm.fullWidth >= 768) {
        vm.breakpoints = 2;
      } else if (vm.fullWidt >= 576) {
        vm.breakpoints = 1;
      } else {
        vm.breakpoints = 0;
      }
    },
    goback() {
      let vm = this;
      vm.$router.go(-1);
    },
    loadApp() {
      let vm = this;
      var height = vm.fullHeight * 0.8;
      var width = (height * 4) / 3;
      var flipbook = $(".flipbook");
      $(".flipbook .double").scissor();
      // Create the flipbook
      $(".flipbook").turn({
        // Elevation
        //elevation: 50,
        // Enable gradients
        gradients: true,
        // Auto center this flipbook
        autoCenter: true
      });
    }
  }
};
</script>

